import { Routes, RouterModule } from "@angular/router";

export const appRoutes: Routes = [
    {path: "", loadChildren: () => require("es6-promise!./views/home/module")("SubModule") },
    {path: "sub", loadChildren: () => require("es6-promise!./views/sub/module")("SubModule") },
    {path: "me", loadChildren: () => require("es6-promise!./views/me/module")("SubModule") },

];
export const routing = RouterModule.forRoot(appRoutes, {useHash: true});



